<template>
    <div>
        <headers :title="title"></headers>
        <div class="set-pay">
           <p>请设置支付密码，用于支付验证</p>
           <div class="clearfix" ref="focus">
                <input v-model="a" class="fl bor-left" maxlength="1" type="number"/>
                <input v-model="b" class="fl bor-middle"  maxlength="1" type="number"/>
                <input v-model="c" class="fl bor-middle" maxlength="1" type="number"/>
                <input v-model="d" class="fl bor-middle" maxlength="1" type="number"/>
                <input v-model="e" class="fl bor-middle" maxlength="1" type="number"/>
                <input class="fl bor-right" maxlength="1" type="number"/>
           </div>
        </div>
    </div>
</template>

<style lang="less" scoped>
    .set-pay{
        font-family: "PingFang-SC-Medium";
        margin-top: 90px;
        background: #fff;
        p{
            padding-top: 90px;
            font-size: 31px;
            color: #000;
            padding-bottom: 90px;
            text-align: center;
        }
        div{
            padding-left: 100px;
            input{
                width: 90px;
                height: 90px;
                text-align: center;
            }
            .bor-left{
                border: 1px solid #D4D4D6;
                border-right: 0;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
            }
            .bor-right{
                border: 1px solid #D4D4D6;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px;
            }
            .bor-middle{
                border: 1px solid #D4D4D6;
                border-right: 0;
            }
        }

    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
export default {
  components:{
      headers
  },
  data(){
      return{
          title: "设置支付密码",
          a: '',
          b: '',
          c: '',
          d: '',
          e: ''
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(255,255,255)";
  },
  watch: {
    a: function () {
      this.$refs.focus.children[1].focus();
    },
    b: function () {
      this.$refs.focus.children[2].focus();
    },
    c: function () {
      this.$refs.focus.children[3].focus();
    },
    d: function () {
      this.$refs.focus.children[4].focus();
    },
    e: function () {
      this.$refs.focus.children[5].focus();
    }
  }
}
</script>



